<template>
	<div class="Binding">
		<header><i class="iconfont icon-xiangzuo1" @click="toLogin"></i><span>微信登录绑定</span></header>
		<div class="container">
			<div>
				<span>关联后，您的WMS帐号和手机帐号都可以登录</span>
			</div>

			<div>
				<i class="iconfont icon-yonghuming"></i>
				<input class='userName'  ref="userName"  @focus="g_focus('userName')"   style="text-transform:none;"   @keyup.enter="getFocus('password')"  v-model="userName"  placeholder='请输入账号' ></input>
				<i v-if="userName" @click="clearInput('userName')" class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<i class="iconfont icon-lock" ></i>
				<input class='pswd' type='password'  ref="password"  @focus="g_focus('password')"   v-model="password"  @keyup.enter="bind"  placeholder='请输入密码' ></input>
				<i v-if="password" @click="clearInput('password')" class="iconfont icon-cuowu"></i>
			</div>
			<button class='bind' @click='bind'>关联</button>
		</div>

		<div v-if="tipsHide"  @touchmove.prevent  id="g_tips"   class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p   @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
		
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"Binding",
		data(){
			return{

				userName:"",//用户名
				password:"",//密码
				tipsHide:false,//提示框是否出现
				msg:"",//提示信息
				inputFocus:"",//遮罩层点击确认后 input聚焦
			}
		},
		methods:{
			toLogin:function(){
				this.$router.push({
					name:"Login",
				})
			},

			bind:function(){

				var that = this;
				
				if(!this.userName){

					that.inputFocus = "userName";
					that.tipsShow("password");
					that.msg = "用户名不能为空";
					
					return;

				}else if(!this.password){

					that.inputFocus = "password";
					that.tipsShow("password");
					that.msg = "密码不能为空";
					
					return;
				}

				var currentUrl = this.HOST + "RFWeixinAuthorize.do";

				var postData = {
					"OpenId":this.$store.state.Bind_Openid,
					"UserID":this.userName,
					"Password":this.password,
				}

				this.httpRequest(currentUrl,postData,function(res){
					console.log(res)

					that.selectMShow = true;

					that.WarehousesList = res.data.data;

				},"userName")
			}
		}
	}
</script>
<style lang="less" scoped>

.Binding{
	.container{
		font-size: 30/50rem;
		width: 100%;
		float: left;

		&>div{
			width: 100%;
			position: relative;
			span{
				margin: 20/50rem auto;
				margin-left: 58/50rem;
				margin-right: 10/50rem;
				display: block;
			}

			.userName,.pswd{
				width:638/50rem;
				height:70/50rem;
				border-radius: 10/50rem;
				padding-left: 82/50rem;
				font-size: 35/50rem;
				box-sizing: border-box;
				margin: 20/50rem auto;
				display: block;
			}

			i.icon-yonghuming,i.icon-lock{
				position: absolute;
				top:15/50rem;
				left:80/50rem;
				color: #265AC3;
				font-size: 40/50rem;
				font-weight: 500;
			}
			i.icon-cuowu{
				position:absolute;
				right:70/50rem;
				font-size: 37/50rem;
				top: 15/50rem;
			}
		}

		.bind{
			width:640/50rem;
			height:73/50rem;
			line-height: 73/50rem;
			font-size: 35/50rem;
			background-color:#285DCE;
			color: #fff;
			margin: 20/50rem auto;
			margin-top: 80/50rem;
			border:none;
			border-radius: 10/50rem;
			outline: none;
			display: block;
		}
	}
}	
</style>


