<template>
	<div class="SelectOrder">
		<header><i class="iconfont icon-xiangzuo1" @click="toCycleCount"></i><span>选择盘点单</span></header>
		<div class="container">

			<table cellspacing="0">
				<thead>
					<tr>
						<td>选择</td>
						<td>盘点单号</td>
						<td>盘点类型</td>
						<td>创建日期</td>
						<td>创建人</td>
						<td>创建人</td>
					</tr>
				</thead>
				<tbody>
					<tr v-for='(dataItem,index) in CycleCount_RFCountSel'  :id="index"  :key='index'  @click="selectData(index)">
						<td class="iconfont"  :class="{ 'icon-xuanzhong':index==detail_item,'icon-weixuanzhong':index!=detail_item}"></td>
						<td>{{dataItem.CountNo}}</td>
						<td>{{dataItem.CountType}}</td>
						<td>{{dataItem.AddDate}}</td>
						<td>{{dataItem.AddWho}}</td>
						<td>{{dataItem.CountStatus}}</td>
					</tr>
				</tbody>
			</table>

			<div class="kong"></div>
			<p class="positionFixed"><span class="g_singleBtn" @click="selectData_submit">确定</span></p>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"SelectOrder",
		data(){
			return{
				CycleCount_RFCountSel:[],//列表数据
				detail_item:0,//默认为第一个
			}
		},
		created(){
			console.log(this.$store.state.CycleCount_RFCountSel)

			var CycleCount_RFCountSel = this.$store.state.CycleCount_RFCountSel;

			this.CycleCount_RFCountSel = CycleCount_RFCountSel;
		},
		methods:{
			// 返回上一页 盘点页面
			toCycleCount:function(){

				this.$router.push({
					name:"CycleCount",
				})
			},
			//点击选择具体订单
			selectData:function(idx){

				this.detail_item = idx;

				
			},
			// 点击确认按钮 跳转到盘点页面
			selectData_submit:function(){


				var CountNo = this.CycleCount_RFCountSel[this.detail_item].CountNo       //盘点单号
				var CountType =  this.CycleCount_RFCountSel[this.detail_item].CountType  //盘点类型

				this.$router.push({
					name:"CycleCount",
					query:{
						SelectOrder:{
							CountNo:CountNo,
							CountType:CountType
						}
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.SelectOrder{
		&>.container{
			width:100%;
			font-size: 30/50rem;
			padding-bottom: 130rpx;
			table{
				tr{
					td:nth-child(2){
						min-width: 150/50rem;
					}
					td:nth-child(3){
						min-width: 150/50rem;
					}
					td:nth-child(4){
						min-width: 320/50rem;
					}
					td:nth-child(5){
						min-width: 120/50rem;
					}
					td:nth-child(6){
						min-width: 120/50rem;
					}
				}
			}
		}
	}
</style>


