<template>

	<div class="Putaway">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>上架</span><i class="iconfont icon-saoma" @click="scan"></i></header>

		<div class="container">

			<div>
				<span>托盘编号</span>
				<input ref="PID" placeholder="请输入内容" v-model="PID" @keyup.enter="confirmOnclick" @focus="g_focus('PID')" @blur="g_blur()"></input>
				<i v-if="PID" @click="clearInput('PID')" class="iconfont icon-cuowu"></i>
			</div>

			<div>
				<span>库位代码</span>
				<input ref="Loc" placeholder="请输入内容" v-model="Loc" @keyup.enter="confirmOnclick" @focus="g_focus('Loc')" @blur="g_blur()"></input>
				<i v-if="Loc" @click="clearInput('Loc')" class="iconfont icon-cuowu"></i>
			</div>

			<span class="g_singleBtn" @click="confirmOnclick">确认</span>
		</div>

		<div v-if="tipsHide"  @touchmove.prevent class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p @ref="inputFocus" @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>

	</div>
</template>
<script type="text/javascript">

	export default{
		name:"Putaway",
		data(){
			return{		
				PID:'',         //测试托盘 	P1807050003
				Loc:'STAGE',
				tipsHide:false, //提示信息是否显示
				msg:'',     //提示信息
				inputFocus:"",
			}
		},
		mounted() {

			this.$refs.PID.focus();
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
		},
		methods:{
			// 接收iOS/android值
			QRScanAjax:function (qrcode, inputName){

				this[inputName] = qrcode;
				if(inputName == "PID") {

					this.confirmOnclick();
				}else if(inputName == "Loc") {

					this.confirmOnclick();
				}
			},
			confirmOnclick(){

				var that = this;

				if(this.PID==''){
					
					this.msg = "托盘号不能为空";
					that.tipsShow("PID");
					that.inputFocus = "PID";
				}else{
					if(this.Loc==''){

						this.msg = "库位代码不能为空";
						that.tipsShow("Loc");
						that.inputFocus = "Loc";
					}else{

						var currentUrl = this.HOST + "RFPutawayQuery.do";

						var postData = {
							"Loc":this.Loc,
							"PID":this.PID,
						}

						this.httpRequest(currentUrl,postData,function(res){
							that.$router.push({
								name:"PutawayDetails",
								query:{
									listData:res.data
								}
							})
						},"PID")
					}
				}
			},
			//返回上一页  首页
			toIndex:function(){
				this.$router.push({
					name:"Index",
				})
			},
		}
	}
</script>
<style scoped lang="less">
.Putaway{
	.container{
		font-size: 30/50rem;
		&>div{
			width: 100%;
			height: 80/50rem;
			margin-top: 20/50rem;
			padding: 25/50rem 20/50rem;
			box-sizing: border-box;
			position: relative;

			span{
				width: 140/50rem;
				text-align: right;
			}

			input::placeholder{
				font-size: 30/50rem;
			}
			
			input{
				margin-left: 10/50rem;
				width: 490/50rem;
				height: 55/50rem;
				padding: 0 10/50rem;
			}
			i{
				position:absolute;
				right:90/50rem;
				font-size: 30/50rem;
				top: 42/50rem;
			}
		}
		&>div:nth-child(2){
			margin-bottom: 50/50rem;
		}
	}
}

// 374像素以下（包含374像素）
@media screen and (max-width: 374px){

	input{
		border:5/50rem solid #bbb;
	}
}


// 255像素以下(包含255像素)
@media screen and (max-width: 255px){
	.Putaway{
		.container{
			&>div{
				i{
					right:60/50rem;
				}
			}
		}
	}
}
</style>


