<template>
	<div class="ScanCommodityCode">
		<header><i class="iconfont icon-xiangzuo1" @click="goPrev"></i><span>扫描商品条码</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div  class="import">
				<span>商品代码</span>
				<input  placeholder="请输入内容"  ref="CommodityCode"  style="text-transform:none;"   @keyup.enter="submit"   v-model="CommodityCode"></input>
				<i v-if="CommodityCode" @click="clearInput('CommodityCode')"  class="iconfont icon-cuowu"></i>
				<i class="iconfont icon-sousuo"  @click="ToSummary" ></i>
			</div>
			<span class="g_singleBtn" @click="submit">确认</span>
			<div v-if="tipsHide" @touchmove.prevent class="g_tips">
				<div>
					<span>错误信息</span>
					<span>{{msg}}</span>
					<p  @click='tipsHideen(inputFocus)'>确定</p>
				</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"ScanCommodityCode",
		data(){
			return{
				CommodityCode:'',//商品代码
				tipsHide:false,//提示框是否出现
				msg:"",//提示信息
				inputFocus:"",
			}
		},
		mounted() {
			this.$refs['CommodityCode'].focus();
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
		},
		created(){
			if(this.$route.query.input_CommodityCode){

				this.CommodityCode = this.$route.query.input_CommodityCode;
			}
		},
		methods:{
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				this[inputName] = qrcode;

				this.submit();
			},
			// 返回上一页
			goPrev:function(){

				var that = this;

				this.$router.push({
					name:"Receipt",
					query:{
						ReceiptKey:that.$store.state.Receipt_SubmitData[0].receiptkey,//收货单号
						ExternalReceiptKey2:that.$store.state.Receipt_SubmitData[0].externalreceiptkey2,//客户单号
					}
				})
			},
			submit:function(){

				var that = this;

				// 获取最近的一个收货批次
				// var LottableUrl = this.HOST + "RFReceiptSkuLottable.do";

				// var Lottable_Data = {
				// 	ReceiptKey : that.$store.state.Receipt_SubmitData[0].receiptkey,//收货单号
				// 	UserID : that.$store.state.UserID,//用户名
				// 	Sku : that.CommodityCode//商品代码
				// }

				// this.httpRequest(LottableUrl,Lottable_Data,function(res){

				// 	if(res.data.data.length){
							
				// 		// that.$store.state.Receipt_PrevBatchProp = res.data.data;
						
				// 	}else{
				// 		that.tipsShow();

				// 		that.msg = "没有数据";
				// 	}
				// })

				// 
				var SummaryUrl = this.HOST + "RFReceiveSku.do";

				var sum_Data = {
					ReceiptKey : that.$store.state.Receipt_SubmitData[0].receiptkey,//收货单号
					StorerKey : that.$store.state.Receipt_SubmitData[0].storerkey,//客户代码
					Sku : that.CommodityCode//商品代码
				}

				this.httpRequest(SummaryUrl,sum_Data,function(res){

					if(res.data.data.length){
							
						that.$store.state.RFReceiveSkuData = res.data.data;

						that.$router.push({
							name:"TakeDelivery",
							query:{
								Sku : that.CommodityCode//商品代码
							}
						})
						
					}else{
						that.tipsShow();

						that.msg = "没有数据";
					}
				})
			},
			//跳转到汇总页面（公用页面）  选择商品
			ToSummary:function(){

				var that = this;

				var SummaryUrl = this.HOST + "RFReceiptSum.do";

				var sum_Data = {
					ReceiptKey : that.$store.state.Receipt_SubmitData[0].receiptkey,
					ExternalReceiptKey2 : that.$store.state.Receipt_SubmitData[0].externalreceiptkey2
				}

				this.httpRequest(SummaryUrl,sum_Data,function(res){

					if(res.data.data.length){
							
						that.$store.state.Receipt_SummaryData = res.data.data;

						that.$router.push({
							name:"Summary",
							query:{
								title:"选择商品",
								ifSelect:true,
								TakeDelivery:{
									ExternalReceiptKey2 : that.CommodityCode
								}
							}
						})
					}else{
						that.tipsShow("CommodityCode");

						that.msg = "没有数据";

						that.inputFocus = "CommodityCode";
					}
				},"CommodityCode")
			},
		}
	}
</script>
<style lang="less" scoped>
.ScanCommodityCode{
	&>.container{
		font-size: 30/50rem;
		input::placeholder{
			font-size: 25/50rem;
		}
		input{
			font-size: 25/50rem;
		}
		&>div.import{
			width:100%;
			height: 80/50rem;
			padding: 25/50rem 20/50rem 0;
			box-sizing: border-box;
			position: relative;
			span{
				width: 140/50rem;
				text-align: right;
			}
			input{
				margin-left: 10/50rem;
				width:490/50rem;	
				height:55/50rem;
				padding:0 10/50rem;
			}
			i{
				position:absolute;
				right:90/50rem;
				font-size: 30/50rem;
				top: 36/50rem;
			}
			i.icon-sousuo{
				right:30/50rem;
				font-size: 40/50rem;
				top: 30/50rem;
			}
			
		}
		.g_singleBtn{
			margin-top: 80/50rem;
		}
	}
}
@media screen and (max-width: 320px){
	.ScanCommodityCode{
		&>.container{
			font-size: 38/50rem;
			input::placeholder{
				font-size: 35/50rem;
			}
			input{
				font-size: 35/50rem;
			}
			&>div.import{
				height: 100/50rem;
				line-height: 100/50rem;
				input{
					width:445/50rem;	
					height:65/50rem;
					margin: 0;
				}
				i{
					position:absolute;
					right:120/50rem;
					font-size: 42/50rem;
					top: 26/50rem;
				}
				i.icon-sousuo{
					right:25/50rem;
					font-size:50/50rem;
					top: 28/50rem;
				}
				
			}
		}
	}
}
@media screen and (max-width: 277px){
	.ScanCommodityCode{
		&>.container{
			font-size: 42/50rem;
			input::placeholder{
				font-size: 40/50rem;
			}
			input{
				font-size: 40/50rem;
			}
			&>div.import{
				height: 100/50rem;
				line-height: 100/50rem;
				input{
					width:440/50rem;	
					height:70/50rem;
					margin: 0;
				}
				i{
					position:absolute;
					right:110/50rem;
					font-size: 48/50rem;
					top: 26/50rem;
				}
				i.icon-sousuo{
					right:20/50rem;
					font-size:56/50rem;
					top: 28/50rem;
				}
				
			}
		}
	}
}
</style>


