//http请求
Vue.prototype.httpRequest=function (url,params,success,focusInput){

	var that = this;

	this.$axios.post(url,
		{
	      "params": JSON.stringify(params),
	      // "WarehouseCode": "KDY-SZ",
	      "WarehouseCode": that.$store.state.WarehouseCode,
	      // "UserID":"55"
	      "UserID":that.$store.state.UserID
	    },
	    {
	    	headers: {
	            'Content-Type': 'application/x-www-form-urlencoded',
	            "AuthCode":"cyscm",
	            "AuthID":''
	        }
	    }
	)
	.then(function(res){
		console.log(res)

		if(res.data.status==1){

			success(res);

		}else{
			that.tipsShow(focusInput);

			if(focusInput){

				that.inputFocus = focusInput;
			}

			if(res.data.Msg){

				that.msg = res.data.Msg;

			}else{

				that.msg = "查询失败";
			}
		}
	})
	.catch(function(res){

		that.tipsShow(focusInput);

		if(focusInput){
				
			that.inputFocus = focusInput;
		}
			
		that.msg = "请求失败";
		
	})
}


