	}

    @media screen and (max-width: 329px) and (min-width: 291px) {
   		.TakeDelivery{
			.container{
				font-size: 32/50rem;
				padding-top: 5/50rem;
				input{
					font-size: 25/50rem;
				}
				&>div{
					height: 50/50rem;
					line-height: 55/50rem;
					margin-bottom: 5/50rem;
					&>div{
						span{
							width: 130/50rem;
							margin-right: 10/50rem;
						}
						input{
							height:50/50rem;
							padding:0 10/50rem;
							box-sizing: border-box;
						}
					}
					.icon-cuowu{
						right:35/50rem;
						top: 0/50rem;
					}
					select{
						font-size: 25/50rem;
					}
					&.select{
						height: 55/50rem;
					}
					input{
						height:50/50rem;
						padding:0 10/50rem;
						box-sizing: border-box;
					}
				}
				&>.goodsName{
					margin-bottom: 5/50rem;
					&>span{
						width: 130/50rem;
						margin-right: 10/50rem;
					}
					&>textarea{
						font-size: 25/50rem;
						padding:0 10/50rem;
					}
				}
			}
		}
    }

    @media screen and (max-width: 290px){
    	.TakeDelivery{
			.container{
				padding-top: 10/50rem;
				input{
					font-size: 25/50rem;
				}
				&>div{
					height: 65/50rem;
					&>div{
						span{
							width: 150/50rem;
							margin-right: 0;
						}
						input{
							width:212/50rem;
							height:55/50rem;
						}
					}
					&.select{
						height: 70/50rem;
					}
					select{
						width:570/50rem;
						height:60/50rem;
						font-size: 25/50rem;
					}
					&>span{
						width: 160/50rem;
						margin-right: 10/50rem;
					}
					&>input{
						width:570/50rem;
						height:55/50rem;
					}
				}
				&>.goodsName{
					margin-bottom: 10/50rem;
					&>span{
						width: 150/50rem;
						margin-right: 0;
					}
					&>textarea{
						width: 588/50rem;
						font-size: 25/50rem;
						padding:0 10/50rem;
						margin-left: 0;
					}
				}
			}
		}
    }
</style>


